<template >
  <div>
    <div class="listaPedido">
      <div v-for="(pedido, index) in miPedido" :key="index" class="container">
        <p>
          <!-- <small class="notify-cantidad">{{ pedido.cantidad }}</small> -->
          <img
            style="background: #9d2d27"
            :src="pedido.imagen"
            alt="Card image cap"
            class="imagenLeft"
          />
          <b-button
            variant="danger"
            style="float: right"
            @click="borrarPedido(index)"
            >X</b-button
          >

          <small> {{ pedido.tama√±o }} ({{ pedido.tipoSticker }})</small>
          <small>
            ${{ calcularPrecio(pedido.tama√±o,pedido.tipoSticker) }} x {{ pedido.cantidad }}</small
          >
          <b-form-spinbutton
            style="margin-left: 4px"
            id="sb-small"
            size="sm"
            inline
            step="1"
            v-model="pedido.cantidad"
          ></b-form-spinbutton>
        </p>
      </div>
    </div>
    <div>
      <div style="text-align: right">
        <div>
          <a
            v-if="calcularCantidad() > 1"
            @click="borrarTodos()"
            class="vaciarCarro"
            >Vaciar carrito <b-icon icon="trash2"
          /></a>
        </div>
        <br />
        <div>
          <small v-if="calcularCantidad() < 5" style="color: red"
            >Debe elegir como minimo 5 stickers</small
          >
        </div>
        <div
          v-if="calcularCantidad() > 5"
          style="width: 50%; text-align: justify"
        ></div>
        <div>
          <small style="float: left">
            Cantidad: {{ calcularCantidad() }}
          </small>
          <small
            ><b> Total : ${{ formatPrice(calcularTotal())}} </b></small
          >
        </div>
      </div>
      <b-button
        class="mt-2"
        variant="success"
        block
        :disabled="calcularCantidad() < 5"
        @click="completarDatosPersonales()"
        >Confirmar pedido</b-button
      >
    </div>
    <div class="modalDatosPersonales">
      <b-modal
        title="Mis datos"
        class="modalCarrito"
        centered
        ref="modalDatosPersonales"
        hide-footer
        body-bg-variant="light"
        footer-bg-variant="light"
        header-bg-variant="light"
      >
        <DatosPersonales
          @okDetallesPersonales="okDetallesPersonales"
          :cantidad="calcularCantidad()"
          :total="calcularTotal()"
          :message="armarPedido()"
        ></DatosPersonales>
      </b-modal>
    </div>
  </div>
</template>

<script>
import DatosPersonales from "@/components/Carrito/DatosPersonales.vue";
export default {
  name: "Carrito",
  components: { DatosPersonales },
  props: {
    miPedido: {
      type: Array,
    },
  },
  data() {
    return {
      cantidad: 1,
      loading: true,
      pedidoOrdenado: [],
      mini: 105,
      regular: 210,
      grande: 260,
      mayor15unidadesMini: 95,
      mayor15unidadesRegular: 190,
      mayor15unidadesGrande: 235,
      mayor50unidadesMini: 75,
      mayor50unidadesRegular: 150,
      mayor50unidadesGrande: 180,
      holograficomini: 145,
      holograficoregular: 280,
      holograficogrande: 330,
      holograficomayor15unidadesMini: 130,
      holograficomayor15unidadesRegular: 250,
      holograficomayor15unidadesGrande: 295,
      holograficomayor50unidadesMini: 95,
      holograficomayor50unidadesRegular: 190,
      holograficomayor50unidadesGrande: 235,
    };
  },
  created() {
  },
  methods: {
    formatPrice(value) {
      return value.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.');
    },
    cambiarCantidad(pedido) {
      pedido.cantidad = Number(pedido.cantidad) + Number(this.cantidad);
    },
    armarPedido() {
      let pedidoMail = "";
      this.miPedido.forEach((pedido) => {
        pedidoMail =
          pedidoMail +
          " " +
          pedido.categoria +
          " " +
          pedido.numero +
          " " +
          "Tama√±o:" +
          " " +
          pedido.tama√±o +
          " " +
          "Cantidad:" +
          " " +
          pedido.cantidad +
          " " +
          "Material:" +
          " " +
          pedido.tipoSticker +
          "\n";
      });

      return pedidoMail;
    },

    calcularCantidad() {
      let cantidad = 0;
      this.miPedido.forEach((pedido) => {
        cantidad = Number(cantidad) + Number(pedido.cantidad);
      });
      return cantidad;
    },
      calcularPrecio(tama√±o,tipo) {
       /* let cantMini=0 ,cantRegular=0,cantGrande=0 */
        let cantidad=0
       this.miPedido.forEach((pedido) => {
          cantidad = cantidad + pedido.cantidad
          //Deprecated. ahora solo importa en general la cantidad, no por tama√±o
         /*  if (pedido.tama√±o == "Mini") {
           cantMini= cantMini +pedido.cantidad
          }
          if (pedido.tama√±o == "Regular") {
            cantRegular= cantRegular +pedido.cantidad
          }
          if (pedido.tama√±o == "Grande") {
            cantGrande = cantGrande + pedido.cantidad
          } */
      });  
        if (tama√±o == "Mini") {
           if (cantidad > 14  && cantidad < 50 ){
              return tipo == 'Blanco' ? this.mayor15unidadesMini : this.holograficomayor15unidadesMini;
            }
            if ( cantidad > 49 ){
              return tipo == 'Blanco' ? this.mayor50unidadesMini : this.holograficomayor50unidadesMini ;
            }            
             return tipo == 'Blanco' ? this.mini : this.holograficomini;
             
        }
        if (tama√±o == "Regular") {
           if (cantidad >14 && cantidad < 50 ){
              return tipo == 'Blanco' ? this.mayor15unidadesRegular : this.holograficomayor15unidadesRegular ;
            }
             if ( cantidad > 49 ){
              return tipo == 'Blanco' ? this.mayor50unidadesRegular : this.holograficomayor50unidadesRegular ;
            }        
             return tipo == 'Blanco' ? this.regular : this.holograficoregular;
             
        }
        if (tama√±o == "Grande") {
                if (cantidad >14 && cantidad < 50 ){
              return  tipo == 'Blanco' ? this.mayor15unidadesGrande : this.holograficomayor15unidadesGrande;
            }
             if ( cantidad > 49 ){
              return  tipo == 'Blanco' ? this.mayor50unidadesGrande : this.holograficomayor50unidadesGrande;
            }    
             return  tipo == 'Blanco' ? this.grande : this.holograficogrande;
            
        }
    },
    calcularTotal() {
      let cantidadTotal = this.calcularCantidad();
      let total = 0; 
      this.miPedido.forEach((pedido) => {
        let tipo = pedido.tipoSticker
        if (pedido.tama√±o == "Mini") {
          if (cantidadTotal > 14 && cantidadTotal < 50) {
            total =
              Number(total) +
              Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mayor15unidadesMini : this.holograficomayor15unidadesMini);
          }
          if (cantidadTotal > 49) {
            total =
              Number(total) +
              Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mayor50unidadesMini : this.holograficomayor50unidadesMini);
          }
          if (cantidadTotal <= 14) {
            total = Number(total) + Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mini : this.holograficomini);
          }
        }
        if (pedido.tama√±o == "Regular") {
          if (cantidadTotal > 14   && cantidadTotal < 50) {
            total =
              Number(total) +
              Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mayor15unidadesRegular : this.holograficomayor15unidadesRegular );
          }
          if (cantidadTotal > 49) {
            total =
              Number(total) +
              Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mayor50unidadesRegular : this.holograficomayor50unidadesRegular);
          }
          if (cantidadTotal <= 14) {
            total =
              Number(total) + Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.regular : this.holograficoregular);
          }
        }
        if (pedido.tama√±o == "Grande") {
            if (cantidadTotal > 14  && cantidadTotal < 50 ) {
            total =
              Number(total) +
              Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mayor15unidadesGrande : this.holograficomayor15unidadesGrande);
          }
          if (cantidadTotal > 49) {
            total =
              Number(total) +
              Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.mayor50unidadesGrande : this.holograficomayor50unidadesGrande);
          }
          if (cantidadTotal <= 14) {
            total =
              Number(total) + Number(pedido.cantidad) * Number(tipo == 'Blanco' ? this.grande : this.holograficogrande);
          }
        }
      });

      return total;
    },

    borrarPedido(pedidoBorrar) {
      this.miPedido.splice(pedidoBorrar, 1);
    },
    borrarTodos() {
      this.miPedido.splice(0, this.miPedido.length);
    },
    completarDatosPersonales() {
      this.$refs["modalDatosPersonales"].show();
    },
    okDetallesPersonales() {
      this.$refs["modalDatosPersonales"].hide();
      this.$emit("okDetalles");
    },
  },
};
</script>

